import './imports.js';
import './sprite.js';
import '../../node_modules/fslightbox';

// import '../img/sprite.svg';
let menuButton = document.querySelector('.menu__button');
let menu = document.querySelector('.menu__content');

document.addEventListener('click', (evt) => {
  if (evt.target.closest('.menu__button')) {
    menu.classList.toggle('menu__content--open');
  }
  else menu.classList.remove('menu__content--open');
});


// Tabs

function toggleTabs(id) {
  let tabsPanel = document.querySelector(`#${id} .tabs__panel`);
  let tabs = document.querySelectorAll('.tabs__tab');
  let contents = document.querySelectorAll('.tabs__content');

  tabsPanel.addEventListener('click', (e) => {
    e.preventDefault();
    tabs.forEach((item, index) => {
      // if (!contents[index]) {
      //   return;
      // }
      if (e.target !== item) {
        item.classList.remove('tabs__tab--active');
        contents[index].classList.remove('tabs__content--active');
        return;
      }

      item.classList.add('tabs__tab--active');
      if (contents[index]) {
        contents[index].classList.add('tabs__content--active');
      }


    })
  })
};

toggleTabs('priceTabs');
